<template>
  <div class="editor">
    <div ref="toolbar" class="toolbar"></div>
    <div ref="editor" class="text"></div>
  </div>
</template>

<script>
import E from "wangeditor";
export default {
  data() {
    return {
      editor: null,
      info_: null
    };
  },
  model: {
    prop: "value",
    event: "change",
    value:null,
  },
  watch: {
    value: function(value) {
      if (value !== this.editor.txt.html()) {
        this.editor.txt.html(this.value);
      }
    }
  },
  mounted() {
    this.seteditor();
    this.editor.txt.html(this.value);
  },
  methods: {
    seteditor() {
      this.editor = new E(this.$refs.toolbar, this.$refs.editor);
      this.editor.customConfig.uploadImgShowBase64 = true; // base 64 
      // this.editor.customConfig.uploadImgMaxSize = 1 * 1024 * 1024; //  2M
      // this.editor.customConfig.uploadImgMaxLength = 6; //  3 
      // this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000; // 
      // 
      this.editor.customConfig.menus = [
        "head", // 
        "bold", // 
        "fontSize", // 
        "fontName", // 
        "italic", // 
        "underline", // 
        "strikeThrough", // 
        "foreColor", // 
        "backColor", // 
        "link", // 
        "list", // 
        "justify", // 
        "quote", // 
        "emoticon", // 
        "image", // 
        "table", // 
        "code", // 
        "undo", // 
        "redo", // 
      ];
      this.editor.customConfig.onchange = html => {
        this.info_ = html; 
        this.$emit("change", this.info_); 
      };
      this.editor.create();
    }
  }
};
</script>

<style scoped>
.editor {
  width: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}
.toolbar {
  border: 1px solid #ccc;
}
.text {
  border: 1px solid #ccc;
  min-height: 500px;
}
</style>